.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: black;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    width: 100%;
}


.messageSender_top{
    display: flex;
    border-bottom: 1px solid rgb(22, 22, 22);
    padding: 15px;
}

.messageSender_top > form{
    flex:1;
    display: flex;
}

.messageSender_top > form > input{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    color: aqua;
    background-color: rgb(22, 22, 22);
}

.messageSender_top > form > button{
    display: none;
}

.messageSender_input{
    flex: 1;
}

.messageSender_bottom{
    display: flex;
    justify-content: space-evenly;
}

.messageSender_option{
    padding: 20px;
    display: flex;
    align-items:center;
    color:gray;
    margin: 5px;
}

.messageSender_option >h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender_option > img{
    height: 60px;
}
.messageSender_option1 > img{
    height: 70px;
}